import React, {
    useCallback,
    useEffect,
    useRef,
    useState,
    forwardRef,
    ForwardedRef
} from 'react';
import {
    Room,
    RoomEvent,
    Participant,
    RemoteParticipant,
    RemoteTrack,
    Track,
    LocalTrack,
    LocalVideoTrack,
    LocalAudioTrack,
    createLocalAudioTrack,
    createLocalVideoTrack,
    VideoPreset,
    VideoPresets,
    TrackPublication,
    LocalParticipant
} from 'livekit-client';
import {
    Box,
    Button,
    Typography,
    Grid,
    Alert,
    IconButton,
    CircularProgress,
    Stack,
    SxProps,
    Theme
} from '@mui/material';
import {
    Mic as MicIcon,
    MicOff as MicOffIcon,
    ScreenShare as ScreenShareIcon,
    StopScreenShare as StopScreenShareIcon,
    Videocam as VideoIcon,
    VideocamOff as VideoOffIcon,
    CallEnd as CallEndIcon,
    Settings as SettingsIcon,
    VolumeUp as VolumeUpIcon,
    VolumeOff as VolumeOffIcon,
    Fullscreen as FullscreenIcon,
    FullscreenExit as FullscreenExitIcon,
    Refresh as RefreshIcon
} from '@mui/icons-material';
import { Log } from '@fanfanlo';
import { mainModel } from '@src/libs/mc/main/MainModel';
import { MeetingRoomProps, VideoPresetConfig, ExtendedParticipantState, ParticipantState } from './meeting-room.intf';

// 
const logger = new Log(false, 'livekit-meeting.MeetingRoom');
const uiLogger = logger.sub(false, 'MeetingRoom_ui');

// 
const VIDEO_PRESET: VideoPresetConfig = {
    width: 1280,
    height: 720,
    // @ts-ignore - frameRate is used by LiveKit internally
    frameRate: 24,
    maxBitrate: 3000 * 1000, // 3Mbps
};

//  meetint-room.intf.ts


function getTokenEndpoint(): string {
    if (!mainModel.appConfig?.livekit?.jwt) {
        throw new Error('LiveKit JWT endpoint is not configured');
    }
    return `${mainModel.appConfig.livekit.jwt}/token`;
}
// 
function MeetingRoomComponent(
    {
        roomName,
        userName,
        onDisconnect,
        onError,
        className = '',
        style,
        sx
    }: MeetingRoomProps,
    ref: ForwardedRef<HTMLDivElement>
) {
    // 
    const [globalMediaState, setGlobalMediaState] = useState<{
        isAudioEnabled: boolean;
        isVideoEnabled: boolean;
    }>({
        isAudioEnabled: true,
        isVideoEnabled: true,
    });

    // Refs
    const roomRef = useRef<Room | null>(null);
    const localTracksRef = useRef<LocalTrack[]>([]);
    const videoElements = useRef<Record<string, HTMLVideoElement>>({});
    const audioElements = useRef<Record<string, HTMLAudioElement>>({});
    const [participantStates, setParticipantStates] = useState<Record<string, ExtendedParticipantState>>({});
    const isUnmountedRef = useRef(false);

    // State
    const [isConnecting, setIsConnecting] = useState(false);
    const [hasConnected, setHasConnected] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [isMuted, setIsMuted] = useState(false);
    const [isVideoEnabled, setIsVideoEnabled] = useState(true);
    const [participants, setParticipants] = useState<RemoteParticipant[]>([]);

    //  token
    const fetchToken = useCallback(async (roomName: string, identity: string) => {
        try {
            const response = await fetch(getTokenEndpoint(), {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    room_name: roomName,
                    identity,
                }),
            });

            if (!response.ok) {
                throw new Error(' token ');
            }

            const data = await response.json();
            return data.token;
        } catch (error) {
            uiLogger.error(' token :', error);
            throw error;
        }
    }, []);

    // 
    const connectToRoom = useCallback(async () => {
        if (isConnecting || hasConnected) return;

        setIsConnecting(true);
        setError(null);

        try {
            // 1.  token
            const token = await fetchToken(roomName, userName);

            // 2. 
            const { audioTrack, videoTrack } = await createLocalTracks();
            localTracksRef.current = [audioTrack, videoTrack];

            // 3. 
            const room = new Room({
                adaptiveStream: true,
                dynacast: true,
                videoCaptureDefaults: {
                    resolution: VIDEO_PRESET,
                },
                // @ts-ignore - publishDefaults is a valid option
                publishDefaults: {
                    dtx: true,
                    red: true,
                    forceStereo: false,
                    simulcast: true,
                } as any, // Type assertion to bypass type checking for now
            });

            // 4. 
            setupRoomListeners(room);

            // 5. 
            await room.connect(
                mainModel.appConfig!.livekit.wsUrl,
                token,
                {
                    autoSubscribe: true,
                }
            );

            // 6. 
            try {
                await Promise.all([
                    room.localParticipant.publishTrack(audioTrack),
                    room.localParticipant.publishTrack(videoTrack)
                ]);
            } catch (publishError) {
                uiLogger.error(':', publishError);
                // 
            }

            // 7. 
            roomRef.current = room;
            setHasConnected(true);

            // 8. 
            const remoteParticipants = Array.from(room.remoteParticipants.values());
            setParticipants(remoteParticipants);

            // 9. 
            updateParticipantState(room.localParticipant.identity, {
                isAudioEnabled: true,
                isVideoEnabled: true,
                audioTrack,
                videoTrack,
            });

            // 10. 
            remoteParticipants.forEach(participant => {
                // 
                initParticipantState(participant);
                uiLogger.log(':', participant.identity);
                
                //  getTrackPublications 
                participant.getTrackPublications().forEach(publication => {
                    if (publication.track && publication.track instanceof RemoteTrack) {
                        handleTrackSubscribed(publication.track, publication, participant);
                    }
                });
            });
            
            // 
            if (room.localParticipant) {
                initParticipantState(room.localParticipant);
                uiLogger.log(':', room.localParticipant.identity);
            }

            uiLogger.log('');

        } catch (error) {
            uiLogger.error(':', error);
            setError('');
            // 
            cleanupTracks(localTracksRef.current);
            localTracksRef.current = [];

            if (roomRef.current) {
                try {
                    await roomRef.current.disconnect();
                } catch (e) {
                    uiLogger.error(':', e);
                }
                roomRef.current = null;
            }
        } finally {
            setIsConnecting(false);
        }
    }, [roomName, userName, isConnecting, hasConnected, fetchToken]);

    // 
    const createLocalTracks = useCallback(async () => {
        try {
            const [audioTrack, videoTrack] = await Promise.all([
                createLocalAudioTrack({
                    echoCancellation: true,
                    noiseSuppression: true,
                    autoGainControl: true,
                }),
                createLocalVideoTrack({
                    resolution: {
                        width: 1280,
                        height: 720,
                        frameRate: 24,
                    },
                }),
            ]);
            return { audioTrack, videoTrack };
        } catch (error) {
            uiLogger.error(':', error);
            throw error;
        }
    }, []);

    // 
    const cleanupTracks = useCallback((tracks: LocalTrack[]) => {
        tracks.forEach(track => {
            track.stop();
            track.detach();
        });
    }, []);

    // 
    const trackElementsRef = useRef<Record<string, boolean>>({});
    const videoElementsRef = useRef<Record<string, HTMLVideoElement>>({});
    const audioElementsRef = useRef<Record<string, HTMLAudioElement>>({});

    // 
    const updateParticipantState = useCallback((identity: string, state: Partial<ParticipantState>) => {
        setParticipantStates(prev => ({
            ...prev,
            [identity]: {
                ...(prev[identity] || {
                    isAudioEnabled: true,
                    isVideoEnabled: true,
                    isAudioEnabledGlobal: true,
                    isVideoEnabledGlobal: true,
                }),
                ...state,
            },
        }));
    }, []);
    // 
    const handleTrackSubscribed = useCallback((
        track: RemoteTrack,
        publication: TrackPublication,
        participant: Participant
    ) => {
        if (!track) return () => { };

        const { identity } = participant;
        const isVideoTrack = track.kind === Track.Kind.Video;

        uiLogger.log(` ${track.kind} :`, identity, track);

        // 
        const updateTrackState = () => {
            const isMuted = track.isMuted;
            const stateUpdate: Partial<ParticipantState> = {};

            if (track.kind === Track.Kind.Audio) {
                stateUpdate.isAudioEnabled = !isMuted;
            } else if (track.kind === Track.Kind.Video) {
                stateUpdate.isVideoEnabled = !isMuted;
            }

            updateParticipantState(identity, stateUpdate);

            //  ParticipantVideo 
            if (isVideoTrack) {
                setParticipants(prev => [...prev]);
            }
        };

        // /
        track.on('muted', updateTrackState);
        track.on('unmuted', updateTrackState);

        // 
        updateTrackState();

        // 
        // 
        setParticipants(prev => {
            const participantExists = prev.some(p => p.identity === identity);
            if (!participantExists) {
                return [...prev, participant as RemoteParticipant];
            }
            return [...prev];
        });


        // 
        return () => {
            track.off('muted', updateTrackState);
            track.off('unmuted', updateTrackState);
        };
    }, [updateParticipantState]);

    // 
    const setupRoomListeners = useCallback((room: Room) => {
        // 
        type EventHandler = (...args: any[]) => void;
        const eventHandlers: Record<string, EventHandler> = {
            // 
            participantConnected: (participant: RemoteParticipant) => {
                if (!(participant instanceof RemoteParticipant)) return;

                uiLogger.log(':', participant.identity);

                // 
                setParticipants(prev => {
                    // 
                    if (prev.some(p => p.identity === participant.identity)) {
                        return prev;
                    }
                    return [...prev, participant];
                });

                // 
                const handleTrackPublished = (publication: TrackPublication) => {
                    uiLogger.log(':', publication.kind, participant.identity);
                    if (publication.track) {
                        handleTrackSubscribed(publication.track as RemoteTrack, publication, participant);
                    } else {
                        publication.once('subscribed', (track: RemoteTrack) => {
                            handleTrackSubscribed(track, publication, participant);
                        });
                    }
                };

                // 
                participant.on('trackPublished', handleTrackPublished);

                // 
                participant.trackPublications.forEach(publication => {
                    if (publication.track) {
                        handleTrackSubscribed(publication.track as RemoteTrack, publication, participant);
                    }
                });

                // 
                return () => {
                    participant.off('trackPublished', handleTrackPublished);
                };
            },

            // 
            participantDisconnected: (participant: RemoteParticipant) => {
                uiLogger.log(':', participant.identity);
                setParticipants(prev => {
                    const newParticipants = prev.filter(p => p.identity !== participant.identity);
                    uiLogger.log(':', newParticipants.map(p => p.identity));
                    return newParticipants;
                });
            },

            // 
            trackSubscribed: (track: RemoteTrack, publication: TrackPublication, participant: Participant) => {
                if (!track) return;
                uiLogger.log(':', track.kind, participant.identity);
                // 
                handleTrackSubscribed(track, publication, participant as RemoteParticipant);
            },

            disconnected: (reason?: any) => {
                const reasonStr = reason?.toString?.() || '';
                uiLogger.log(':', reasonStr);
                //  handleDisconnect
                setHasConnected(false);
                setParticipants([]);
                setError(null);
            },

            reconnecting: () => {
                uiLogger.log('...');
                setError('...');
            },

            reconnected: () => {
                uiLogger.log('');
                setError(null);
            }
        };

        // 
        room
            .on(RoomEvent.Disconnected, eventHandlers.disconnected)
            .on(RoomEvent.Reconnecting, eventHandlers.reconnecting)
            .on(RoomEvent.Reconnected, eventHandlers.reconnected)
            .on(RoomEvent.ParticipantConnected, eventHandlers.participantConnected)
            .on(RoomEvent.ParticipantDisconnected, eventHandlers.participantDisconnected)
            .on(RoomEvent.TrackSubscribed, eventHandlers.trackSubscribed);

        // 
        return () => {
            if (!room) return;
            room
                .off(RoomEvent.Disconnected, eventHandlers.disconnected)
                .off(RoomEvent.Reconnecting, eventHandlers.reconnecting)
                .off(RoomEvent.Reconnected, eventHandlers.reconnected)
                .off(RoomEvent.ParticipantConnected, eventHandlers.participantConnected)
                .off(RoomEvent.ParticipantDisconnected, eventHandlers.participantDisconnected)
                .off(RoomEvent.TrackSubscribed, eventHandlers.trackSubscribed);

            //  void
            return undefined;
        };
    }, [handleTrackSubscribed]);

    // 
    const leaveMeeting = useCallback(async () => {
        uiLogger.log('...');
        try {
            // 
            cleanupTracks(localTracksRef.current);

            // 
            if (roomRef.current) {
                await roomRef.current.disconnect();
                roomRef.current = null;
            }

            // 
            setHasConnected(false);
            setParticipants([]);
            setError(null);

            // 
            Object.values(videoElements.current).forEach(el => el && el.remove());
            Object.values(audioElements.current).forEach(el => el && el.remove());
            videoElements.current = {};
            audioElements.current = {};

            // 
            if (onDisconnect) {
                onDisconnect();
            }

            uiLogger.log('');
        } catch (error) {
            uiLogger.error(':', error);
            setError('');
        }
    }, [onDisconnect]);

    // 
    const cleanup = useCallback(async (isUnmounting = false) => {
        // 
        if (isUnmounting) {
            isUnmountedRef.current = true;
        }

        uiLogger.log('...');
        if (!roomRef.current) return;

        const localParticipant = roomRef.current.localParticipant;
        const audioTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Audio
        ) as LocalAudioTrack | undefined;
        const videoTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Video
        ) as LocalVideoTrack | undefined;

        try {
            if (audioTrack) {
                await audioTrack.stop();
            }
            if (videoTrack) {
                await videoTrack.stop();
            }
        } catch (error) {
            uiLogger.error(':', error);
        }

        // 
        cleanupTracks(localTracksRef.current);
        localTracksRef.current = [];

        // 
        if (roomRef.current) {
            roomRef.current.disconnect();
            roomRef.current = null;
        }

        // 
        setHasConnected(false);
        setParticipants([]);
        setParticipantStates({});
    }, []);

    // 
    const updateGlobalMediaState = useCallback((audioEnabled: boolean, videoEnabled: boolean) => {
        setGlobalMediaState(prev => ({
            ...prev,
            isAudioEnabled: audioEnabled,
            isVideoEnabled: videoEnabled,
        }));
    }, []);

    // 
    const toggleMute = useCallback(async () => {
        if (!roomRef.current) return;

        const localParticipant = roomRef.current.localParticipant;
        const audioTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Audio
        ) as LocalAudioTrack | undefined;

        if (!audioTrack) return;

        try {
            // 
            const newGlobalAudioState = !globalMediaState.isAudioEnabled;

            // 
            if (newGlobalAudioState) {
                await audioTrack.unmute();
            } else {
                await audioTrack.mute();
            }

            // UI
            setIsMuted(!newGlobalAudioState);
            updateParticipantState(localParticipant.identity, { isAudioEnabled: newGlobalAudioState });

            // 
            updateGlobalMediaState(newGlobalAudioState, globalMediaState.isVideoEnabled);
        } catch (error) {
            uiLogger.error(':', error);
            setError('');
        }
    }, [globalMediaState, updateParticipantState, updateGlobalMediaState]);

    // 
    const toggleVideo = useCallback(async () => {
        if (!roomRef.current) return;

        const localParticipant = roomRef.current.localParticipant;
        const videoTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Video
        ) as LocalVideoTrack | undefined;

        if (!videoTrack) return;

        try {
            // 
            const newGlobalVideoState = !globalMediaState.isVideoEnabled;

            // 
            if (newGlobalVideoState) {
                await videoTrack.unmute();
            } else {
                await videoTrack.mute();
            }

            // UI
            setIsVideoEnabled(newGlobalVideoState);
            updateParticipantState(localParticipant.identity, { isVideoEnabled: newGlobalVideoState });

            // 
            updateGlobalMediaState(globalMediaState.isAudioEnabled, newGlobalVideoState);
        } catch (error) {
            uiLogger.error(':', error);
            setError('');
        }
    }, [globalMediaState, updateParticipantState, updateGlobalMediaState]);

    // tile
    const handleLocalMuteToggle = useCallback(async () => {
        if (!roomRef.current) return;

        const localParticipant = roomRef.current.localParticipant;
        const audioTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Audio
        ) as LocalAudioTrack | undefined;

        if (!audioTrack) return;

        try {
            const newMutedState = !isMuted;
            if (newMutedState) {
                await audioTrack.mute();
            } else {
                await audioTrack.unmute();
            }

            setIsMuted(newMutedState);
            updateParticipantState(localParticipant.identity, { isAudioEnabled: !newMutedState });
        } catch (error) {
            uiLogger.error(':', error);
            setError('');
        }
    }, [isMuted, updateParticipantState]);

    // tile
    const handleLocalVideoToggle = useCallback(async () => {
        if (!roomRef.current) return;

        const localParticipant = roomRef.current.localParticipant;
        const videoTrack = localTracksRef.current.find(
            t => t.kind === Track.Kind.Video
        ) as LocalVideoTrack | undefined;

        if (!videoTrack) return;

        try {
            const newVideoState = !isVideoEnabled;
            if (newVideoState) {
                await videoTrack.unmute();
            } else {
                await videoTrack.mute();
            }

            setIsVideoEnabled(newVideoState);
            updateParticipantState(localParticipant.identity, { isVideoEnabled: newVideoState });
        } catch (error) {
            uiLogger.error(':', error);
            setError('');
        }
    }, [isVideoEnabled, updateParticipantState]);

    // 
    useEffect(() => {
        return () => {
            // 
            cleanup(true);
        };
    }, []);

    // 
    const getParticipantState = useCallback((identity: string): ParticipantState => {
        return participantStates[identity] || {
            isAudioEnabled: true,
            isVideoEnabled: true,
            isAudioEnabledGlobal: true,
            isVideoEnabledGlobal: true,
        };
    }, [participantStates]);

    // 
    const initParticipantState = useCallback((participant: Participant) => {
        const state = getParticipantState(participant.identity);

        // 
        const audioTrack = participant.getTrackPublications().find(
            pub => pub.kind === Track.Kind.Audio
        )?.track as LocalAudioTrack | undefined;

        // 
        const videoTrack = participant.getTrackPublications().find(
            pub => pub.kind === Track.Kind.Video
        )?.track as LocalVideoTrack | undefined;

        updateParticipantState(participant.identity, {
            ...state,
            isAudioEnabled: audioTrack ? !audioTrack.isMuted : false,
            isVideoEnabled: videoTrack ? !videoTrack.isMuted : false
        });
    }, [getParticipantState, updateParticipantState]);

    // 
    const handleToggleAudio = useCallback((participant: Participant) => {
        const currentState = participantStates[participant.identity] || {
            isAudioEnabled: true,
            isVideoEnabled: true,
            isAudioEnabledGlobal: true,
            isVideoEnabledGlobal: true,
        };

        updateParticipantState(participant.identity, {
            ...currentState,
            isAudioEnabled: !currentState.isAudioEnabled
        });
    }, [participantStates, updateParticipantState]);

    // 
    const handleToggleVideo = useCallback((participant: Participant) => {
        const currentState = participantStates[participant.identity] || {
            isAudioEnabled: true,
            isVideoEnabled: true,
            isAudioEnabledGlobal: true,
            isVideoEnabledGlobal: true,
        };

        updateParticipantState(participant.identity, {
            ...currentState,
            isVideoEnabled: !currentState.isVideoEnabled
        });
    }, [participantStates, updateParticipantState]);

    // 
    const getEffectiveMediaState = useCallback((participantId: string) => {
        const state = participantStates[participantId];
        if (!state) return { isAudioEnabled: true, isVideoEnabled: true };

        return {
            isAudioEnabled: globalMediaState.isAudioEnabled ? state.isAudioEnabled : false,
            isVideoEnabled: globalMediaState.isVideoEnabled ? state.isVideoEnabled : false,
        };
    }, [globalMediaState, participantStates]);

    // 
    const ParticipantVideo = React.memo(({
        participant,
        isLocal,
        isVideoEnabled
    }: {
        participant: Participant;
        isLocal: boolean;
        isVideoEnabled: boolean;
    }) => {
        const videoRef = useRef<HTMLVideoElement>(null);
        const [videoTrack, setVideoTrack] = useState<RemoteTrack | LocalVideoTrack | null>(null);

        // 
        useEffect(() => {
            if (!participant) return;

            // 
            const videoPublication = Array.from(participant.trackPublications.values())
                .find(pub => pub.kind === Track.Kind.Video);

            // 
            if (videoPublication && videoPublication.track) {
                setVideoTrack(videoPublication.track);
            }

            // 
            const handleTrackSubscribed = (track: Track) => {
                if (track.kind === Track.Kind.Video) {
                    setVideoTrack(track as RemoteTrack | LocalVideoTrack);
                }
            };

            // 
            const handleTrackUnsubscribed = (track: Track) => {
                if (track.kind === Track.Kind.Video) {
                    setVideoTrack(null);
                }
            };

            // 
            participant.on(RoomEvent.TrackSubscribed, handleTrackSubscribed);
            participant.on(RoomEvent.TrackUnsubscribed, handleTrackUnsubscribed);

            // 
            return () => {
                participant.off(RoomEvent.TrackSubscribed, handleTrackSubscribed);
                participant.off(RoomEvent.TrackUnsubscribed, handleTrackUnsubscribed);
            };
        }, [participant]);

        // /
        useEffect(() => {
            const videoElement = videoRef.current;
            if (!videoElement || !videoTrack) return;

            // 
            while (videoElement.firstChild) {
                videoElement.removeChild(videoElement.firstChild);
            }

            // 
            if (isVideoEnabled) {
                videoTrack.attach(videoElement);
            }

            return () => {
                if (videoTrack) {
                    videoTrack.detach(videoElement);
                }
            };
        }, [videoTrack, isVideoEnabled]);

        return (
            <Box
                sx={{
                    width: '100%',
                    aspectRatio: '16/9',
                    bgcolor: 'grey.800',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    position: 'relative',
                }}
            >
                <video
                    ref={videoRef}
                    autoPlay
                    playsInline
                    muted={isLocal}
                    style={{
                        width: '100%',
                        height: '100%',
                        objectFit: 'cover',
                        display: isVideoEnabled ? 'block' : 'none'
                    }}
                />
                {!isVideoEnabled && (
                    <Box
                        sx={{
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            right: 0,
                            bottom: 0,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            bgcolor: 'grey.900',
                        }}
                    >
                        <Box
                            sx={{
                                width: 80,
                                height: 80,
                                borderRadius: '50%',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                bgcolor: 'primary.main',
                                color: 'white',
                                fontSize: 32,
                                fontWeight: 'bold',
                            }}
                        >
                            {participant.identity.charAt(0).toUpperCase()}
                        </Box>
                    </Box>
                )}
            </Box>
        );
    });

    // 
    const ParticipantAudio = React.memo(({
        participant,
        isMuted,
    }: {
        participant: Participant;
        isMuted: boolean;
    }) => {
        const audioRef = useRef<HTMLAudioElement>(null);
        const [audioTrack, setAudioTrack] = useState<RemoteTrack | LocalAudioTrack | null>(null);

        useEffect(() => {
            if (!participant) return;

            const handleTrackSubscribed = (track: Track) => {
                if (track.kind === Track.Kind.Audio) {
                    setAudioTrack(track as RemoteTrack | LocalAudioTrack);
                }
            };

            const handleTrackUnsubscribed = (track: Track) => {
                if (track.kind === Track.Kind.Audio) {
                    setAudioTrack(null);
                }
            };

            // 
            const audioPublication = Array.from(participant.trackPublications.values())
                .find(pub => pub.kind === Track.Kind.Audio);

            if (audioPublication && audioPublication.track) {
                setAudioTrack(audioPublication.track);
            }

            participant.on(RoomEvent.TrackSubscribed, handleTrackSubscribed);
            participant.on(RoomEvent.TrackUnsubscribed, handleTrackUnsubscribed);

            return () => {
                participant.off(RoomEvent.TrackSubscribed, handleTrackSubscribed);
                participant.off(RoomEvent.TrackUnsubscribed, handleTrackUnsubscribed);
            };
        }, [participant]);

        useEffect(() => {
            const audioElement = audioRef.current;
            if (audioElement && audioTrack) {
                audioTrack.attach(audioElement);
                // 
                audioElement.muted = isMuted;
                return () => {
                    audioTrack.detach(audioElement);
                };
            }
        }, [audioTrack, isMuted]);

        return <audio ref={audioRef} autoPlay playsInline />;
    });

    // 
    const renderParticipant = useCallback((participant: Participant, isLocal: boolean) => {
        const effectiveState = getEffectiveMediaState(participant.identity);

        return (
            <Box
                key={participant.identity}
                sx={{
                    position: 'relative',
                    borderRadius: 2,
                    overflow: 'hidden',
                    bgcolor: 'background.paper',
                    boxShadow: 1,
                }}
            >
                {/*  */}
                <ParticipantVideo
                    participant={participant}
                    isLocal={isLocal}
                    isVideoEnabled={effectiveState.isVideoEnabled}
                />

                {/*  */}
                {!isLocal && (
                    <ParticipantAudio
                        participant={participant}
                        isMuted={!effectiveState.isAudioEnabled}
                    />
                )}

                {/*  */}
                <Box
                    sx={{
                        position: 'absolute',
                        bottom: 0,
                        left: 0,
                        right: 0,
                        p: 1,
                        bgcolor: 'rgba(0, 0, 0, 0.6)',
                        display: 'flex',
                        justifyContent: 'center',
                        gap: 1,
                    }}
                >
                    <IconButton
                        size="small"
                        color={effectiveState.isAudioEnabled ? 'primary' : 'error'}
                        onClick={() => handleToggleAudio(participant)}
                    >
                        {effectiveState.isAudioEnabled ? <MicIcon /> : <MicOffIcon />}
                    </IconButton>
                    <IconButton
                        size="small"
                        color={effectiveState.isVideoEnabled ? 'primary' : 'error'}
                        onClick={() => handleToggleVideo(participant)}
                    >
                        {effectiveState.isVideoEnabled ? <VideoIcon /> : <VideoOffIcon />}
                    </IconButton>
                    <Typography variant="caption" color="white">
                        {participant.identity} {isLocal && '()'}
                    </Typography>
                    )

                    {/*  */}
                    {isLocal && (
                        <IconButton
                            size="small"
                            onClick={handleLocalMuteToggle}
                            color={isMuted ? 'error' : 'primary'}
                            sx={{ color: 'white' }}
                            title={isMuted ? '' : ''}
                        >
                            {isMuted ? <MicOffIcon /> : <MicIcon />}
                        </IconButton>
                    )}

                    {/*  */}
                    {isLocal && (
                        <IconButton
                            size="small"
                            onClick={handleLocalVideoToggle}
                            color={isVideoEnabled ? 'primary' : 'error'}
                            sx={{ color: 'white' }}
                            title={isVideoEnabled ? '' : ''}
                        >
                            {isVideoEnabled ? <VideoIcon /> : <VideoOffIcon />}
                        </IconButton>
                    )}
                </Box>
            </Box>
        );
    }, [isMuted, isVideoEnabled, getEffectiveMediaState, handleToggleAudio, handleToggleVideo, handleLocalMuteToggle, handleLocalVideoToggle]);

    // 
    if (isConnecting) {
        return (
            <Box sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                height: '100%',
                p: 3
            }}>
                <CircularProgress />
                <Typography variant="body1" sx={{ mt: 2 }}>...</Typography>
            </Box>
        );
    }

    // 
    if (error) {
        return (
            <Alert severity="error" sx={{ m: 2 }}>
                <Typography variant="body1">{error}</Typography>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={connectToRoom}
                    startIcon={<RefreshIcon />}
                    sx={{ mt: 1 }}
                >
                    
                </Button>
            </Alert>
        );
    }

    // 
    function renderMainContent() {
        // 
        if (hasConnected && roomRef.current) {
            return (
                <Box className={className} sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                    <Box sx={{ flex: 1, overflow: 'auto', p: 2 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12} md={participants.length === 0 ? 12 : 6}>
                                {renderParticipant(roomRef.current.localParticipant, true)}
                            </Grid>
                            {participants.map(function (participant) {
                                return (
                                    <Grid item xs={12} md={6} key={participant.identity}>
                                        {renderParticipant(participant, false)}
                                    </Grid>
                                );
                            })}
                        </Grid>
                    </Box>
                    {/*  */}
                    <Box sx={{
                        p: 2,
                        borderTop: '1px solid',
                        borderColor: 'divider',
                        display: 'flex',
                        justifyContent: 'center',
                        gap: 2
                    }}>
                        <Button
                            variant="contained"
                            color={!globalMediaState.isAudioEnabled ? 'error' : 'primary'}
                            onClick={toggleMute}
                            startIcon={!globalMediaState.isAudioEnabled ? <MicOffIcon /> : <MicIcon />}
                        >
                            {!globalMediaState.isAudioEnabled ? '' : ''}
                        </Button>

                        <Button
                            variant="contained"
                            color={!globalMediaState.isVideoEnabled ? 'error' : 'primary'}
                            onClick={toggleVideo}
                            startIcon={globalMediaState.isVideoEnabled ? <VideoIcon /> : <VideoOffIcon />}
                        >
                            {globalMediaState.isVideoEnabled ? '' : ''}
                        </Button>

                        <Button
                            variant="contained"
                            color="error"
                            onClick={leaveMeeting}
                            startIcon={<CallEndIcon />}
                        >
                            
                        </Button>
                    </Box>
                </Box>
            );
        }

        // 
        return (
            <Box className={className} sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                p: 3
            }}>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={connectToRoom}
                    disabled={isConnecting}
                    startIcon={<VideoIcon />}
                    sx={{ mt: 2 }}
                >
                    
                    {isConnecting && (
                        <CircularProgress
                            size={16}
                            sx={{
                                color: 'inherit',
                                ml: 1
                            }}
                        />
                    )}
                </Button>
            </Box>
        );
    }

    // 
    return (
        <Box ref={ref} sx={sx} style={style}>
            {error ? (
                <Alert
                    severity="error"
                    action={
                        <Button
                            color="inherit"
                            size="small"
                            onClick={connectToRoom}
                        >
                            
                        </Button>
                    }
                >
                    {error}
                </Alert>
            ) : (
                renderMainContent()
            )}
        </Box>
    );
}

//  forwardRef 
const MeetingRoom = forwardRef(MeetingRoomComponent);
MeetingRoom.displayName = 'MeetingRoom';

export { MeetingRoom };
