import { storeUtils, toProxy } from "@fanfanlo";

export interface ISocketSettingData{
    //
    keepConnect:boolean;
    url:string;
}

async function createDefSocketSettingData():Promise<ISocketSettingData>{
    const data =  {
        keepConnect:false,
        url:""     
     }
     const res = await storeUtils.getNamespaceStoreWithData('market-dev-socket-setting', {defData:data})
     return res.data;
}

export async function getMarketDevSocketSettingData():Promise<ISocketSettingData>{
    return await createDefSocketSettingData()
}